$prefix: '.wc-booster';

#{$prefix}-linked-product-wrapper {
	position: relative;
	// padding: 30px 15px;
	// margin: 0 -10px;

	#{$prefix}-linked-product-card-wrapper {

	    #{$prefix}-linked-product-card-image {
		    background-size: cover;
		    background-position: center;
		    background-repeat: no-repeat;
		    transition: all .6s ease-in-out;
		    transform: scale(1);
		    position: relative;
		    overflow: hidden;
		    
			&:after {
				content: '';
				height: 100%;
				width: 100%;
				position: absolute;
				top: 0;
				right: 0;
				background: #000;
				opacity: 0;
				transition: all .3s;
			}
		}

		#{$prefix}-linked-product-body-inner {
		    padding: 15px 0 0;
		}

		#{$prefix}-linked-product-post-title {
		    margin: 3px 0;
		    font-size: 18px;
		    a {
		    	color: #fff;
		    	text-decoration: none;
			    font-weight: 500;
			    line-height: 28px;
			    font-size: 20px;
		    }
		}

		#{$prefix}-linked-product-price {
		    margin: 10px 0;
		}

		#{$prefix}-linked-product-post-cat {
			margin-top: 3px;
			a {
				color: #fff;
				text-decoration: none;
				position: relative;
				letter-spacing: 1px;
				opacity: .95;
			    font-weight: 400;
			    line-height: 1.2;
			    font-size: 12px;

				.line {
					content: "";
				    width: 20px;
				    height: 2px;
				    background: #fff;
				    position: absolute;
				    top: 50%;
				    transform: translate(0, -50%);
				    left: 108%;
				}

				&:nth-child(n+1) {
					margin-left: 40px;

				}

				&:first-child {
					margin-left: 0;

				}
			}

			
		}

		#{$prefix}-linked-product-meta-wrapper {
		    display: flex;
		    margin-bottom: 15px;

		    div {
		    	padding: 0 10px;

		    	a {
		    		color: #fff;
		    		text-decoration: none;
		    		letter-spacing: 1px;
		    		opacity: .95;
		    		text-transform: capitalize;
				    font-weight: 400;
				    line-height: 1.2;
				    font-size: 12px;
		    	}
		    }

		   	

			i {
				margin-right: 10px;
			}
		    
		} 

		#{$prefix}-linked-product-post-content {
			p {
			    margin: 0;
			    color: #fff;
			}
		}
		
	}

	.slick-slide {
		&:focus {
			outline: none;
		}
	}

	#{$prefix}-slider-arrow {
	    position: absolute;
	    right: 15px;
	    top: -30px;
	    z-index: 9;
	    cursor: pointer;
	    width: 45px;
	    height: 35px;
	    border: none;
	    background: #000;
	    color: #fff;
	    font-size: 21px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    padding: 0;
		border-radius: 6px;
	    
	    &#{$prefix}-prev-arrow {
	    	right: 65px;
	    }
	}

	#{$prefix}-arrow-wide {
		#{$prefix}-slider-arrow {
			right: 8px;
			top: 50%;
			transform: translate(0, -50%);
			&#{$prefix}-prev-arrow {
				left: 8px;
				right: auto;
			}
		}
	}

	ul.slick-dots {
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    list-style: none;
	    margin-bottom: 0;
	    padding-left: 0;
	    li {
	    	button {
	    		border: 1px solid transparent;
			    height: 9px;
			    width: 45px;
			    margin: 0 4px;
			    border-radius: 9px;
			    background: #e7e7e7;
			    cursor: pointer;

			    &:focus {
			    	outline: none;			    	
			    }			    
	    	}

	    	&.slick-active {
	    		button {
					background: #000;
	    		}
	    	}
	    }
	}

	.slick-track {
		margin-left: inherit;
	}
}

#{$prefix}-linked-product-title {
    padding: 0 140px 0 10px;
    color: #fff;
    margin: 0;
    text-align: left;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    font-size: 20px;
}

#{$prefix}-align-center {
    text-align: center;
	a {
		img {
			margin: auto;
		}
	}
}

#{$prefix}-align-left {
    text-align: left;
    .wc-booster-linked-product-rating .star-rating {
    	margin: 0;
    }
}

#{$prefix}-align-right {
    text-align: right;

	a {
		img {
			margin-left: auto;
		}
	}

	 .wc-booster-linked-product-rating .star-rating {
    	margin: 0 0 0 auto;
    }
}


#{$prefix}-linked-product-rating {
    display: block;
	.star-rating {
		float: none !important;
		margin: auto;
	}
}


/* layout two */
.layout-two {
	.wc-booster-linked-product-inner-wrapper {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	
		> div:first-child {
			max-width: 30%;
			flex: 0 0 30%;
			order: 2;
		}
	
		.wc-booster-linked-product-card {
			max-width: 70%;
			flex: 0 0 70%;
			order: 1;
			padding-right: 30px;
			
		}
	
		@media (max-width:620px) {
			> div:first-child {
				max-width: 100%;
				flex: 0 0 100%;
				order: 1;
			}
		
			.wc-booster-linked-product-card {
				max-width: 100%;
				flex: 0 0 100%;
				order: 2;
				padding-right:0;
			}
		}
		&.wc-booster-linked-product-has-banner {
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			.wc-booster-linked-product-card {
				max-width: 100%;
				flex: 0 0 100%;
				order: 2;
				padding-right:0;
			}
		}
	}

	
}

.wc-booster-linked-product-wrapper {
	ul.slick-dots {
	   	display: flex !important;
	    align-items: center;
	    justify-content: center;
	    list-style: none;
	    margin-bottom: 0;

	    li {
	    	
	    	button {
	    		font-size: 0;
		    	color: transparent;
		    	height: 	20px !Important;
		    	width: 20px !Important;
		    	border-radius: 50% !Important;
		    	margin: 0 3px;
		    	background: #eee;
	    	}
	    	&.slick-active {
	    		button {
		    		background: #000;

	    		}
	    	}
	    }
	}
}

.wc-booster-linked-product-btn {
    text-decoration: none;
}

.wc-booster-align-center {
	.wc-booster-linked-product-body-inner {
		margin: auto;
	}
}

.wc-booster-align-left {
	.wc-booster-linked-product-body-inner {
		margin-right: auto;
	}
}

.wc-booster-align-right {
	.wc-booster-linked-product-body-inner {
		margin-left: auto;
	}
}

.woocommerce .wc-booster-linked-product-init .slick-dots button {
    padding: unset;
}

.wc-booster-linked-product-card-wrapper {
	position: relative;
}

.wc-booster-linked-product-card-wrapper:hover .wc-booster-quick-view {
    visibility: visible;
    opacity: 1;
}